import Vue from 'vue'
import Router from 'vue-router'

//
import welcome from '../pages/welcome/welcome.vue'
//
import login from '../pages/login/login.vue'
//
import signin from '../pages/login/signin.vue'
//
import index from '../pages/index.vue'
//
import code404 from '../pages/error/404.vue'

//
//
import Classification from '../pages/food/cat.vue'
// 
import food from '../pages/food/index.vue'


// 
import foodPackage from '../pages/food/package.vue'

//
import statistics from '../pages/statistics/index.vue'
import text from '../pages/text.vue'

//
import user from '../pages/user/user.vue'
import userCat from '../pages/user/userCat.vue'

//
import info from '../pages/user/settingInfo.vue'

Vue.use(Router)

export default new Router({
    routes: [{
            path: '/',
            name: '',
            redirect: 'login',
            component: index,
            children: [{
                    path: '/food',
                    name: '',
                    component: food,
                    meta: {
                        requireAuth: true
                    }
                }, {
                    path: '/Classification',
                    name: '',
                    component: Classification,
                    meta: {
                        requireAuth: true
                    }
                },
                {
                    path: '/foodPackage',
                    name: '',
                    component: foodPackage,
                    meta: {
                        requireAuth: true
                    }
                },
                {
                    path: '/statistics',
                    name: '',
                    component: statistics,
                    meta: {
                        requireAuth: true
                    }
                },
                {
                    path: '/info',
                    name: '',
                    component: info,
                    meta: {
                        requireAuth: true
                    }
                },
                {
                    path: '/user',
                    name: '',
                    component: user,
                    meta: {
                        requireAuth: true
                    }
                },
                {
                    path: '/user_cat',
                    name: '',
                    component: userCat,
                    meta: {
                        requireAuth: true
                    }
                },
                {
                    path: '/welcome',
                    name: '',
                    component: welcome
                },
            ]
        },
        {
            path: '/text',
            name: 'text',
            component: text
        },
        {
            path: '/login',
            name: 'login',
            component: login,
            meta: { requireAuth: true }
        },
        {
            path: '/signin',
            name: 'signin',
            component: signin,
            meta: {
                requireAuth: true
            }
        },
        {
            path: '/error',
            name: 'code404',
            component: code404
        },
        {
            path: "*",
            redirect: "/error"
        }
    ]
})


