/**
 * Validate normal string number
 * @param {*} rule
 * @param {*} value
 * @param {*} callback
 */
const strNumber = (rule, value, callback) => {
  if (value) {
    const errors = []
    if (!regPattern.stringNumber.test(Number(value))) {
      errors.push(new Error(''))
      callback(errors)
    } else {
      callback()
    }
  } else {
    callback()
  }
}

